.Footer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    width: calc(100vw - 17px);
    color: white;
    left: 0;
    position: absolute;
    font-family: OpenSans-Light;
    z-index: 3;
}

.Footer-nav {
    display: -ms-flexbox;
    display: flex;
    min-height: 100;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: #4A5664;
    width: 100%;
    padding: 30px 50px 30px calc(20% + 50px);
}

.Footer-nav-left {
    text-align: left;
}

.Footer-nav-right {
    text-align: right;
}

.Footer-nav-left a,
.Footer-nav-right a {
    text-decoration: none;
}

.Footer-nav-label {
    color: #73D2FF;
    font-size: 14px;
    height: 20px;
    padding: 0;
    margin: 0 0 10px 0;
}

.Footer-nav-link {
    padding: 0;
    margin: 0;
    font-size: 24px;
    font-family: OpenSans-Semibold;
    border-bottom: 1px solid #9DA1A6;
    height: 35px;
    -webkit-transition: border 0.1s ease-in;
    -o-transition: border 0.1s ease-in;
    transition: border 0.1s ease-in;
    color: white;
}

.Footer-nav-link:hover {
    cursor: pointer;
    border-bottom: 2px solid #FFFFFF;
}

.Footer-bottom {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    background-color: #384655;
    width: 100%;
    height: 80px;
    padding: 30px 50px 30px calc(20% + 50px);
}

.Footer-bottom-content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    height: 50px;
}

.Footer-bottom-left {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.Footer-bottom-left span {
    margin-right: 10px;
}

.Footer-logo {
    height: 30px;
}


/*toggle to fixed*/

.Footer-fixed-nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: rgba(74, 86, 100, 0.5);
    width: calc(100vw - 17px);
    left: 0;
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 20px 50px 20px 330px;
    z-index: 2;
    -webkit-transition: background-color 0.2s ease;
    -o-transition: background-color 0.2s ease;
    transition: background-color 0.2s ease;
}

.Footer-fixed-nav .Footer-nav-label {
    margin: 0;
}

.Footer-fixed-nav:hover {
    background-color: rgba(74, 86, 100, 0.9);
}

.Footer-fixed-nav .Footer-nav-left .Footer-nav-label:hover,
.Footer-fixed-nav .Footer-nav-right .Footer-nav-label:hover {
    text-decoration: underline;
}


