.Header {
    display: -ms-flexbox;
    display: flex;
    position: fixed;
    -ms-flex-align: center;
        align-items: center;
    background-color: #384655;
    width: calc(100vw - 17px);
    height: 55px;
    top: 0;
    left: 0;
    z-index: 5;
    color: white;
}

.Header>* {
    text-decoration: none;
}

.Header-logo-title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin-right: 100px;
    -webkit-transition: margin-right 0.3s;
    -o-transition: margin-right 0.3s;
    transition: margin-right 0.3s;
}

.Header-title {
    display: -ms-flexbox;
    display: flex;
    color: white;
    font-size: 1.5rem;
    margin-left: 35px;
    font-family: Flexo-Regular;
    -webkit-transition: font-size 0.3s;
    -o-transition: font-size 0.3s;
    transition: font-size 0.3s;
}

.Header-title span {
    font-family: Flexo-Demi;
}

.Header-logo-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 55px;
    height: 55px;
    background: #00aeff;
    -webkit-transition: width 0.3s, height 0.3s;
    -o-transition: width 0.3s, height 0.3s;
    transition: width 0.3s, height 0.3s;
}

.Header-logo {
    height: 37px;
    -webkit-transition: height 0.3s;
    -o-transition: height 0.3s;
    transition: height 0.3s;
}

.Header-right-container {
    display: -ms-flexbox;
    display: flex;
    font-size: 13px;
    position: absolute;
    margin-right: 50px;
    right: 0;
    -ms-flex-align: center;
        align-items: center;
}

.Header-search-form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin-left: 20px;
}

.Header-search-icon {
    margin-right: -25px;
    z-index: 5;
}

.Header-search-form input[type='text'] {
    padding: 6px 12px 6px 35px;
    border-radius: 5px;
    border: none;
    background: #384655;
    width: 183px;
}

.Header-search-form input[type='text']:hover,
.Header-search-form input[type='text']:focus {
    background: #5C6773;
    outline: none;
}

.Header-search-form input,
.Header-search-form textarea {
    color: white;
    font-size: 18px;
}

.Header-search-form input[type='text']::-webkit-input-placeholder {
    color: #D7DDE4;
    font-family: OpenSans-Light;
}

.Header-search-label {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.Header-version {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin-left: 20px;
    background: #5C6773;
    padding: 4px 12px 4px 12px;
    border-radius: 15px;
}

.Header-nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    color: white;
    margin-right: 20px;
    text-align: center;
}

.Header-nav-normal {
    color: #d9d9d9;
    text-decoration: none;
    border-bottom: 6px solid #384655;
    padding: 17px 20px 14px 20px;
}

.Header-nav-normal:hover {
    color: white;
    background: #2E3946;
    border-bottom-color: #2E3946;
}

.Header-nav-normal.Header-nav-active:hover {
    border-bottom-color: #00aeff;
}

.Header-nav-normal:focus {
    color: white;
    font-weight: bold;
    border-bottom: 6px solid #00aeff;
}

.Header-nav-active {
    color: white;
    font-weight: bold;
    border-bottom: 6px solid #00aeff;
}


