.Sidebar-wrapper {
    position: fixed;
    width: 20%;
    height: calc(100vh - 55px);
    margin-top: 55px;
    z-index: 4;
    -webkit-transition: width 0.3s;
    -o-transition: width 0.3s;
    transition: width 0.3s;
    overflow: hidden;
    margin-right: 17px;
    /* border-right: 1px solid #DBDDE0; */
    /* box-shadow: 2px 0 6px rgba(0, 0, 0, .15); */
}

.Sidebar {
    display: -ms-flexbox;
    display: flex;
    background: white;
    height: calc(100vh - 55px);
    overflow-y: auto;
    margin-right: -17px;
}

.Sidebar-content {
    color: #333333;
    margin-top: 30px;
    width: calc(100% - 17px);
}

.Sidebar-group {
    margin-left: 10px;
    margin-right: -17px;
    -webkit-transition: margin-left 0.3s;
    -o-transition: margin-left 0.3s;
    transition: margin-left 0.3s;
    color: #384655;
    white-space: space;
}

.Sidebar-group:last-child {
    margin-bottom: 10px;
}

.Sidebar-group-text {
    margin: 0;
    text-transform: uppercase;
    font-family: OpenSans-Bold;
    font-size: 15px;
    margin-bottom: 10px;
    margin-top: 10px;
    cursor: pointer;
    color: #006d9a;
}

.Sidebar-group-text:hover {
    color: #00aeff;
}

.Sidebar-group ul {
    margin: 0;
    padding: 0;
    list-style: none;
    margin-left: -9px;
    height: 0;
    overflow: hidden;
    opacity: 0;
    -webkit-transition: 0.4s ease;
    -o-transition: 0.4s ease;
    transition: 0.4s ease;
}

.Sidebar-group ul active {
    margin-bottom: 15px;
}

.Sidebar-group li {
    margin: 10px 0 10px 14px;
}

.Sidebar-group-text span {
    float: left;
    width: 15px;
    text-align: center;
}

.Sidebar-group.active>ul {
    opacity: 1;
    height: auto;
}

.Sidebar-list-item {
    white-space: nowrap;
}

.Sidebar-list-item a {
    text-decoration: none;
    display: block;
    font-size: 14px;
    padding: 15px 27px 15px 25px;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    width: 100%;
}

.Sidebar-list-item a:hover {
    color: #00aeff;
}

.Sidebar-list-item-normal {
    color: #333;
    border-left: 6px solid white;
}

.Sidebar-list-item-active {
    border-left-color: #00aeff;
    font-family: OpenSans-Bold;
    color: #00aeff;
    background: #F0F3F6;
}

.Sidebar-icon {
    color: #5E676F;
}

.Sidebar-icon.fa-stack {
    font-size: 8px;
    margin-left: 15px;
}

.Sidebar-icon .fa-stack-text {
    padding-left: 1px;
}

.Sidebar-icon .fa-lock {
    color: #006D9A;
    font-size: 12px;
    margin-left: 8px;
    margin-top: 3px;
}

.Sidebar-list-item.entity:not(:first-of-type),
.Sidebar-list-item.view:not(:first-of-type) {
    border-top: 1px solid #D7DDE4;
    padding-top: 10px;
}

.Sidebar-group .Sidebar-group-level2 {
    margin: 0;
    margin-left: 20px;
}

.Sidebar-group-level2 p {
    font-size: 14px;
}

.Sidebar-group-level2 ul li a {
    font-size: 12px;
}

.Sidebar-group-level2 ul li {
    margin-top: 5px;
    margin-bottom: 5px;
}

.Sidebar-group-level2 ul {
    margin-bottom: 5px;
}

.Sidebar-group-level2 .Sidebar-list-item a {
    padding-top: 5px;
    padding-bottom: 5px;
}

.Sidebar-list {
    margin-bottom: 50px;
}

