.HighlightCode {
    position: relative;
}

.HighlightCode button {
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 1;
    font-size: 18px;
    visibility: hidden;
    opacity: 0;
    width: 30px;
    height: 30px;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
    border-radius: 5px;
    background: rgba(110, 111, 113, 0.5);
    color: #FFFFFF;
    border: none;
}

.HighlightCode:hover button {
    visibility: visible;
    opacity: 1;
}

.HighlightCode button:hover {
    background: #626465;
    cursor: pointer;
    outline: 0;
}

.HighlightCode button:active {
    background: #515351;
    cursor: pointer;
}

.HighlightCode .copied {
    position: absolute;
    top: 31px;
    right: 15px;
}

.HighlightCode .copied-caret {
    position: relative;
    width: 20px;
    height: 20px;
    overflow: hidden;
    top: -13px;
    right: 6px;
}

.HighlightCode .copied-caret:after {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    background: #FFFFFF;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    top: 15px;
    left: 7px;
    -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
}

.HighlightCode .copied-content {
    position: absolute;
    background: #FFFFFF;
    color: #333333;
    font-size: 14px;
    font-weight: bold;
    border-radius: 5px;
    -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
    padding: 5px 15px 5px 15px;
    top: 8px;
    right: 0px;
}

.hljs {
    display: block;
    overflow-x: auto;
    color: #333;
    background: #f8f8f8;
    border-radius: 10px;
    border: 1px solid #a1a1a1;
    padding: 20px 30px 20px 30px;
    font-size: 13px;
    font-family: Consolas, Menlo, "Ubuntu Mono", monospace;
}

.hljs-comment,
.hljs-quote {
    color: #998;
    color: #45BF86;
    font-style: italic
}

.hljs-keyword,
.hljs-selector-tag,
.hljs-subst {
    color: #333;
    font-weight: bold
}

.hljs-number,
.hljs-literal,
.hljs-variable,
.hljs-template-variable,
.hljs-tag .hljs-attr {
    color: #008080
}

.hljs-string,
.hljs-doctag {
    color: #d14
}

.hljs-title,
.hljs-section,
.hljs-selector-id {
    color: #900;
    font-weight: bold
}

.hljs-subst {
    font-weight: normal
}

.hljs-type,
.hljs-class .hljs-title {
    color: #458;
    font-weight: bold
}

.hljs-tag,
.hljs-name,
.hljs-attribute {
    color: #000080;
    font-weight: normal
}

.hljs-regexp,
.hljs-link {
    color: #009926
}

.hljs-symbol,
.hljs-bullet {
    color: #990073
}

.hljs-built_in,
.hljs-builtin-name {
    color: #0086b3
}

.hljs-meta {
    color: #999;
    font-weight: bold
}

.hljs-deletion {
    background: #fdd
}

.hljs-addition {
    background: #dfd
}

.hljs-emphasis {
    font-style: italic
}

.hljs-strong {
    font-weight: bold
}

.hljs::-webkit-scrollbar {
    width: 12px;
}

.hljs::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px #C1C1C1;
    border-radius: 10px;
    background-color: #f8f8f8;
}

.hljs::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #C1C1C1;
}

