.ant-tree {
    font-family: "Monospaced Number", "Chinese Quote", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 16px;
    line-height: 1.5;
    color: rgba(0, 0, 0, 0.65);
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    list-style: none;
    margin: 0;
    padding: 0;
}

.ant-tree ol,
.ant-tree ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.ant-tree li {
    padding: 4px 0;
    margin: 0;
    list-style: none;
    white-space: nowrap;
    outline: 0;
}

.ant-tree li span[draggable],
.ant-tree li span[draggable="true"] {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border-top: 2px transparent solid;
    border-bottom: 2px transparent solid;
    margin-top: -2px;
    /* Required to make elements draggable in old WebKit */
    -khtml-user-drag: element;
    -webkit-user-drag: element;
}

.ant-tree li.drag-over>span[draggable] {
    background-color: #00aeff;
    color: white;
    opacity: 0.8;
}

.ant-tree li.drag-over-gap-top>span[draggable] {
    border-top-color: #00aeff;
}

.ant-tree li.drag-over-gap-bottom>span[draggable] {
    border-bottom-color: #00aeff;
}

.ant-tree li.filter-node>span {
    color: #f5222d !important;
    font-weight: 500 !important;
}

.ant-tree li ul {
    margin: 0;
    padding: 0 0 0 18px;
}

.ant-tree li .ant-tree-node-content-wrapper {
    display: inline-block;
    padding: 0 5px;
    border-radius: 2px;
    margin: 0;
    cursor: pointer;
    text-decoration: none;
    vertical-align: top;
    color: rgba(0, 0, 0, 0.65);
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
    position: relative;
    height: 24px;
    line-height: 24px;
}

.ant-tree li .ant-tree-node-content-wrapper:hover {
    /* background-color: #e6f7ff; */
    color: #00aeff;
}

.ant-tree li .ant-tree-node-content-wrapper.ant-tree-node-selected {
    /* background-color: #bae7ff; */
    background-color: #F0F3F6;
    color: #00aeff;
}

.ant-tree li span.ant-tree-checkbox {
    margin: 4px 4px 0 2px;
}

.ant-tree li span.ant-tree-switcher,
.ant-tree li span.ant-tree-iconEle {
    margin: 0;
    width: 24px;
    height: 24px;
    line-height: 24px;
    display: inline-block;
    vertical-align: middle;
    border: 0 none;
    cursor: pointer;
    outline: none;
    text-align: center;
}

.ant-tree li span.ant-tree-icon_loading {
    position: absolute;
    left: 0;
    top: 1px;
    background: #fff;
    -webkit-transform: translateX(-100%);
    -ms-transform: translateX(-100%);
    transform: translateX(-100%);
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

.ant-tree li span.ant-tree-icon_loading:after {
    display: inline-block;
    font-family: 'anticon';
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\E64D";
    -webkit-animation: loadingCircle 1s infinite linear;
    animation: loadingCircle 1s infinite linear;
    color: #00aeff;
}

.ant-tree li span.ant-tree-switcher.ant-tree-switcher-noop {
    cursor: default;
}

.ant-tree li span.ant-tree-switcher.ant-tree-switcher_open:after {
    font-size: 12px;
    font-size: 7px \9;
    -webkit-transform: scale(0.58333333) rotate(0deg);
    -ms-transform: scale(0.58333333) rotate(0deg);
    transform: scale(0.58333333) rotate(0deg);
    display: inline-block;
    font-family: 'anticon';
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\E606";
    font-weight: bold;
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    -o-transition: transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}

 :root .ant-tree li span.ant-tree-switcher.ant-tree-switcher_open:after {
    font-size: 12px;
}

.ant-tree li span.ant-tree-switcher.ant-tree-switcher_close:after {
    font-size: 12px;
    font-size: 7px \9;
    -webkit-transform: scale(0.58333333) rotate(0deg);
    -ms-transform: scale(0.58333333) rotate(0deg);
    transform: scale(0.58333333) rotate(0deg);
    display: inline-block;
    font-family: 'anticon';
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\E606";
    font-weight: bold;
    -webkit-transition: -webkit-transform .3s;
    transition: -webkit-transform .3s;
    -o-transition: transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}

 :root .ant-tree li span.ant-tree-switcher.ant-tree-switcher_close:after {
    font-size: 12px;
}

.ant-tree li span.ant-tree-switcher.ant-tree-switcher_close:after {
    -webkit-transform: rotate(270deg) scale(0.59);
    -ms-transform: rotate(270deg) scale(0.59);
    transform: rotate(270deg) scale(0.59);
}

.ant-tree li:last-child>span.ant-tree-switcher:before,
.ant-tree li:last-child>span.ant-tree-iconEle:before {
    display: none;
}

.ant-tree>li:first-child {
