    font-family: OpenSans-Light;
    font-style: normal;
    font-weight: 600;
    src: url(fonts/Open_Sans/OpenSans-Light.ttf);
}

@font-face {
    font-family: OpenSans-LightItalic;
    font-style: italic;
    font-weight: 300;
    src: url(fonts/Open_Sans/OpenSans-LightItalic.ttf);
}

@font-face {
    font-family: OpenSans-Regular;
    font-style: normal;
    font-weight: 400;
    src: url(fonts/Open_Sans/OpenSans-Regular.ttf);
}

@font-face {
    font-family: OpenSans-RegularItalic;
    font-style: italic;
    font-weight: 400;
    src: url(fonts/Open_Sans/OpenSans-Italic.ttf);
}

@font-face {
    font-family: OpenSans-Semibold;
    font-style: normal;
    font-weight: 600;
    src: url(fonts/Open_Sans/OpenSans-Semibold.ttf);
}

@font-face {
    font-family: OpenSans-SemiboldItalic;
    font-style: italic;
    font-weight: 600;
    src: url(fonts/Open_Sans/OpenSans-SemiboldItalic.ttf);
}

@font-face {
    font-family: OpenSans-Bold;
    font-style: normal;
    font-weight: 700;
    src: url(fonts/Open_Sans/OpenSans-Bold.ttf);
}

@font-face {
    font-family: OpenSans-BoldItalic;
    font-style: italic;
    font-weight: 700;
    src: url(fonts/Open_Sans/OpenSans-BoldItalic.ttf);
}

@font-face {
    font-family: Flexo-Regular;
    font-style: normal;
    font-weight: 500;
    /* src: url(fonts/Durotype_Flexo/Durotype-Flexo-Regular.otf); */
    src: url(fonts/Open_Sans/OpenSans-Semibold.ttf);
}

@font-face {
    font-family: Flexo-Demi;
    font-style: normal;
    font-weight: 600;
    /* src: url(fonts/Durotype_Flexo/Durotype-Flexo-Demi.otf); */
    src: url(fonts/Open_Sans/OpenSans-Bold.ttf);
}

* {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: OpenSans-Regular;
    background: #f0f3f6;
}

h1 {
    font-size: 2.571rem;
    line-height: 1.2;
    font-weight: 300;
    color: #333;
}

h2 {
    font-size: 1.571rem;
    line-height: 1.5;
    font-weight: 600;
    color: #333;
}

h3 {
    font-size: 1.285rem;
    line-height: 1.5;
    font-weight: 600;
    color: #333;
}

h4 {
    font-size: 1rem;
    line-height: 1.5;
    font-weight: 600;
    color: #333;
}

ul {
    padding: 0
}

.span-flexo {
    font-family: Flexo-Regular;
}

.span-flexo-bold {
    font-family: Flexo-Demi;
}

.Primary-button {
    background: #1AA95A;
    color: white;
    border-radius: 5px;
    font-family: OpenSans-Semibold;
    cursor: pointer;
    text-decoration: none;
}

.Primary-button:hover {
    background: #0F8B4D;
}

.Primary-button:active {
    background: #136C3C;
}

.Primary-button-alt {
    background: #6E53A3;
    color: white;
    border-radius: 5px;
    font-family: OpenSans-Semibold;
    cursor: pointer;
    text-decoration: none;
}

.Primary-button-alt:hover {
    background: #60488F;
}

.Primary-button-alt:active {
    background: #513E7A;
}

.Secondary-button {
    background: #F3F3F4;
    color: #555555;
    border-radius: 5px;
    border: 2px solid #CCCCCC;
    font-family: OpenSans-Semibold;
    cursor: pointer;
    text-decoration: none;
}

.Secondary-button:hover {
    border-color: #BABABA;
    color: #333333;
}

.Secondary-button:active {
    background: #E7E4E3;
    border-color: #CBCCCB;
    color: #767676;
}

.Tertiary-button {
    color: #00AEFF !important;
    font-family: OpenSans-Semibold;
    cursor: pointer;
    text-decoration: none;
}

.Tertiary-button:hover {
    color: #008FD1 !important;
}

.Tertiary-button:active {
    color: #006FA3 !important;
}

article {
    color: #333333;
    margin-bottom: 80px;
    min-height: calc(100vh - 258px);
}

.article-header {
    font-size: 2.571rem;
    margin-bottom: 50px;
    font-family: OpenSans-Light;
    -webkit-transition: font-size 0.3s;
    -o-transition: font-size 0.3s;
    transition: font-size 0.3s;
}

article h5 {
    font-family: OpenSans-Bold;
    font-size: 1.07142857rem;
    text-transform: uppercase;
    color: #006d9a;
    margin: 0 0 20px 0;
    -webkit-transition: font-size 0.3s;
    -o-transition: font-size 0.3s;
    transition: font-size 0.3s;
}

.article-content {
    font-size: 17px;
    line-height: 1.7;
}

.article-intro {
    font-size: 20px;
    line-height: 2;
    font-family: OpenSans-Light;
    margin: 0;
}

svg {
    width: 100%;
    height: 100%;
}

hr {
    border-top: 1px solid #F0F3F6;
    margin: 40px 0 40px 0;
}

.diagram {
    cursor: pointer;
}

.hc-tile {
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 30px 35px;
    background-color: #fff;
    border-radius: 5px;
    display: block;
    -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
            box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
    margin-bottom: 30px;
}

.hc-tile-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.cards {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.hc-card {
    width: calc(50% - 10px);
    min-width: 200px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 20px 25px;
    background-color: #fff;
    border-radius: 5px;
    display: block;
    -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
            box-shadow: 0 2px 5px rgba(0, 0, 0, .2);
    margin-bottom: 20px;
    -webkit-transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
    -o-transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
    transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
    color: #333;
}

.hc-card a {
    text-decoration: none;
    color: #333;
}

.hc-card:hover {
    -webkit-box-shadow: 0 8px 16px rgba(0, 0, 0, .2);
            box-shadow: 0 8px 16px rgba(0, 0, 0, .2);
    cursor: pointer;
}

.hc-card:active {
    -webkit-box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
            box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.hc-card:nth-of-type(odd) {
    margin-right: 10px;
}

.hc-card:nth-of-type(even) {
    margin-left: 10px;
}

.hc-card-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.hc-card-content {
    font-size: 18px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.hc-card-button {
    font-size: 12px;
}

.hc-card hr {
    margin: 15px 0;
    color: #F0F3F6;
}

.hc-card-header h5 {
    margin: 0;
    text-transform: capitalize;
}

.csv-download {
    text-decoration: none;
    color: white;
    font-size: 12px;
    padding: 5px 15px 5px 15px;
}

.tab-bar-horizontal {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    border-bottom: 1px solid #c0c5cc;
    margin-bottom: 25px;
}

.tab-bar-left {
    display: -ms-flexbox;
    display: flex;
}

.tab-item {
    /* font-size: 16px; */
    padding: 0 20px 11px 20px;
    border-bottom: 4px solid #f0f3f6;
    color: #333;
    cursor: pointer;
    text-decoration: none;
}

.tab-item.Tertiary-button {
    padding-left: 0;
    padding-bottom: 5px;
}

.tab-item:hover {
    color: #00aeff;
}

.tab-item:focus {
    color: #006D9A;
}

.tab-item.active {
    border-bottom: 4px solid #00aeff;
    font-weight: 700;
}

.tab-item.active:hover {
    color: #333;
}

.tab-item.active:focus {
    color: #333;
}

.icon-h2 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.icon-img {
    width: 35px;
    height: 100%;
    margin-right: 10px;
}

.icon-span {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.icon-img-span {
    margin-right: 3px;
}

.pl {
    width: 100%;
}

.pl-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 5px 10px;
}

/* .pl-item:hover {
    background: #E4E7EA;
} */

.pl-item:nth-child(odd) {
    background: #F0F3F6;
}

.pl-item-header {
    font-family: OpenSans-Bold;
    width: 25%;
    text-transform: uppercase;
    font-size: 12px;
}

.pl-item-content {
    width: 75%;
    font-size: 14px;
}

.diagram-tile {
    position: relative;
}

.diagram-tile .expand,
.diagram-tile .rotate {
    position: absolute;
    z-index: 1;
    font-size: 18px;
    visibility: hidden;
    opacity: 0;
    width: 30px;
    height: 30px;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
    border-radius: 5px;
    background: rgba(110, 111, 113, 0.5);
    color: #FFFFFF;
    border: none;
    outline: none;
}

.diagram-tile .expand {
    top: 25px;
    right: 25px;
}

.diagram-tile .rotate {
    top: 25px;
    right: 65px;
}

.diagram-tile:hover button {
    visibility: visible;
    opacity: 1;
}

.diagram-tile button:hover {
    background: #626465;
    cursor: pointer;
    outline: 0;
}

.diagram-tile button:active {
    background: #515351;
    cursor: pointer;
}


