.ModalWrapper-blocker {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 10;
    padding: 25px;
}

.ModalWrapper-container {
    background-color: white;
    border-radius: 5px;
    -webkit-box-shadow: 0 0 30px #000000;
            box-shadow: 0 0 30px #000000;
    padding-bottom: 3px;
}

.ModalWrapper-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    font-family: OpenSans-Bold;
    padding: 10px 15px 15px 15px;
}

.ModalWrapper-title {
    margin: 0;
    color: #333333;
    font-size: 18px;
}

.ModalWrapper-divider {
    margin: 0;
}

.ModalWrapper-button {
    font-size: 16px;
    -ms-flex-item-align: end;
        align-self: flex-end;
}


